import React from 'react'
import Card from './components/Card'
import { Container, HeaderTitle, Title, Subtitle } from '../../../../styles'
import { StyleThreeFeatures, Row } from './style'
import { New, User, Group } from '../../../../assets'

const ThreeFeatures: React.FC = () => {
  return (
    <StyleThreeFeatures>
      <Container>
        <HeaderTitle>
          <Title> bem simples</Title>
          <Subtitle>passos simples para conseguir chegar ao seu destino</Subtitle>
        </HeaderTitle>
        <Row>
          <Card
            image={New}
            title="Faa seu cadastro"
            text="Cadastre-se em nossa plataforma,  estremamente fcil, voc ver"
          />
          <Card
            image={User}
            title="Complete seu perfil"
            text="No  obrigatrio, mas um perfil completo passa mais confiana para outros usurios"
          />
          <Card
            image={Group}
            title="Crie novos grupos"
            text="Aps criar uma conta, voc poder criar novos grupos e chamar quem voc quiser"
          />
        </Row>
      </Container>
    </StyleThreeFeatures>
  )
}

export default ThreeFeatures
