import React from 'react'
import { connect, ConnectedProps } from 'react-redux'
import { QuestionsIntroduction } from '../../../../assets'
import { Container, StyledIntroduction, Content } from '../../../../styles'

type Props = PropsFromRedux

const IntroductionQuestions: React.FC<Props> = ({ navbar }) => {
  return (
    <StyledIntroduction image={QuestionsIntroduction} isNav={navbar}>
      <Container>
        <Content>
          <h4>Dvidas</h4>
          <p>Ficou com dvida sobre algo, veja abaixo algumas das dvidas mais frequentes dentre todos que acessam a nossa plataforma. Caso voc no encontre o que precisa, preencha o formulrio e responderemos em breve</p>
        </Content>
      </Container>
    </StyledIntroduction>
  )
}

interface RootState {
  navbar: boolean
}

const mapState = (state: RootState): RootState => ({
  navbar: state.navbar
})

const connector = connect(mapState)

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(IntroductionQuestions)
