import React from 'react'
import Card from './components/Card'
import { StyledKnow, Row } from './style'
import { Container, Title } from '../../../../styles'
import { Purpose, Vision, Values } from '../../../../assets'

const Know: React.FC = () => {
  return (
    <StyledKnow>
      <Container>
        <Title>Conhea-nos melhor</Title>
        <Row>
          <Card
            title="Propsito"
            image={Purpose}
            content="Nossa misso  fazer com que nossos usurios conheam novas pessoas que buscam viajar para o mesmo destino, assim tendo novas e extraordinrias experincias."
          />
          <Card
            title="Ambio"
            image={Vision}
            content="Queremos ser reconhecidos como uma empresa que junta pessoas de mesmos interesses e criar memrias nicas e eternas na vida dos usurios."
          />
          <Card
            title="Valores"
            image={Values}
            content="Temos como princpios a confiana com a nossa plataforma,a praticidade e o conforto.
            O nosso objetivo  que o usurio queira ter essa experincia cada vez mais."
          />
        </Row>
      </Container>
    </StyledKnow>
  )
}

export default Know
