import React from 'react'
import { MdKeyboardArrowLeft } from 'react-icons/md'
import { Container } from '../../styles'
import { Pencil } from '../../assets'
import {
  StyledCreateChat,
  Trace,
  Title,
  OrangeCircle,
  Form,
  Label,
  Input,
  TextArea,
  DivButton,
  Button
} from './style'

const CreateChat: React.FC = () => {
  return (
    <>
      <Trace></Trace>
      <StyledCreateChat>
        <Container>
          <MdKeyboardArrowLeft color="#FF6D00"/>
          <Title>Nova Conversa</Title>
          <Form>
            <OrangeCircle>
              <img src={Pencil} alt="Pencil"/>
            </OrangeCircle>
            <Label htmlFor="name">Nome:</Label>
            <Input id="name" name="name" placeholder="Ex: Frias no Hava"></Input>
            <Label htmlFor="description">Descrio:</Label>
            <TextArea id="description" name="description" placeholder="Ex: Grupo para passar frias no Hava do dia 20 a 30."></TextArea>
            <DivButton>
              <Button type="submit">Criar</Button>
            </DivButton>
          </Form>
        </Container>
      </StyledCreateChat>
    </>
  )
}

export default CreateChat
