import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { connect, ConnectedProps } from 'react-redux'
import ArrowLeft from '../../../../assets/icons/arrow-right.png'
import Image from '../../../../assets/images/home/home_introduction.png'
import { StyledIntroduction, Row } from './style'
import { Container } from '../../../../styles'
import { FcCheckmark } from 'react-icons/fc'

type Props = PropsFromRedux

const Introduction: React.FC<Props> = ({ navbar }) => {
  const [UseTerms, setUseTerms] = useState(false)
  return (
    <StyledIntroduction image={Image} useTerms={UseTerms} isNav={navbar}>
      <Container>
        <Row>
          <div className="wrapper_content">
            <h5>Querendo viajar para algum lugar mas no tem ningum para te acompanhar?</h5>
            <p>Aqui na tcxp voc conhece pessoas novas e com os mesmos interesses que voc. Crie grupos de viagens e chame pessoas para fazerem parte dele.</p>
            <Link to="/sobre">Conhea mais</Link>
          </div>
          <div className="wrapper_form">
            <form>
              <div className="wrapper_title">
                <p>Cadastre-se</p>
              </div>
              <div className="wrapper_fields">
                <input
                  type="text"
                  placeholder="Nome completo"
                  name="name"
                />
                <input
                  type="email"
                  placeholder="Email"
                  name="email"
                />
                <input
                  type="password"
                  placeholder="Senha"
                  name="password"
                />
                <input
                  type="password"
                  placeholder="Confirmar senha"
                  name="passwordConfirmation"
                />
                <div className="wrapper_submit">
                  <div className="wrapper_checkbox">
                    <FcCheckmark onClick={() => setUseTerms(!UseTerms)}/>
                    <label className="field_checbox" htmlFor="useTerms">
                      <input
                        type="checkbox"
                        id="useTerms"
                        name="useTerms"
                        onChange={() => setUseTerms(!UseTerms)}
                      />
                    </label><label htmlFor="useTerms">Termos de uso</label>
                  </div>
                  <button type="button">
                    <img src={ArrowLeft} alt=""/>
                  </button>
                </div>
              </div>
            </form>
          </div>
        </Row>
      </Container>
    </StyledIntroduction>
  )
}

interface RootState {
  navbar: boolean
}

const mapState = (state: RootState): RootState => ({
  navbar: state.navbar
})

const connector = connect(mapState)

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(Introduction)
