import React from 'react'
import BackImage from '../../../../assets/images/home/back_decision.png'
import { StyledDecision } from './style'

const Decision: React.FC = () => {
  return (
    <StyledDecision image={BackImage}>
      <div className="content">
        <h4>Voc decide o que fazer!</h4>
        <p>Voc pode escolher a qualquer hora e em qualquer lugar sem  depender de ningum para te aconpanhar nesta jornada</p>
        <button>Cadastre-se</button>
      </div>
    </StyledDecision>
  )
}

export default Decision
